.gallery-images {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  margin: 0px;
  padding: 0px;
}
.gallery-images.no-images {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.gallery-image {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  border: 1px solid rgba(0, 0, 0, 0);
  width: calc(calc(470px / 3));
  height: calc(calc(470px / 3));
  overflow: hidden;
}
.gallery-image:hover {
  /* border: 1px solid white; */
  opacity: 0.8;
  cursor: pointer;
}
.gallery-image .media {
  width: 100%;
  min-width: calc(calc(470px / 3));
  min-height: calc(calc(470px / 3));
}
.gallery-images .alert.alert-info {
}



