.edit-image {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.edit-image .image-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.image-container {
  max-width: calc(100vw - 30px);
  max-height: calc(100vw - 20%);
}

.image-container .media {
  -ms-flex: 1 1;
      flex: 1 1;
  max-width: 100%;
  height: auto;
  max-height: calc(100vw - 30vw);
  border: 5px solid rgba(255, 255, 255, 1);
}

.edit-image-tags {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.edit-image-tags .tag:not(:last-child) {
  margin-right: 5px;
}

.click-top-copy {
  margin-top: 5px;
  font-size: 0.8em;
  font-weight: bolder;
  color: white;
}



