html,
body {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 500px;
  /*min-height: 500px;*/
}
a {
  color: white;
}
#app {
  display: block;
  width: 100%;
}
.gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*min-height: 500px;*/
}
.add-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.form-row {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}
.form-row img {
  max-width: 50%;
}

.gallery-content {
  padding: 15px;
  /* background-color: #25354f; */
  -ms-flex: 1 1;
      flex: 1 1;
}

.top-button-bar {
  margin-bottom: 15px;
}

.content-section:not(:last-child) {
  margin-bottom: 15px;
}

.gallery-images video.media {
  -o-object-fit: cover;
     object-fit: cover;
}

input {
  border: 2px solid #444;
  padding: 8px;
  border-radius: 4px;
  font-size: 14px;
  color: #25354f;
  background-color: rgba(255, 255, 255, 0.7);
}
input:focus {
  background-color: rgba(255, 255, 255, 1);
}



